
body{
  padding: 0;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
}
#app{
  display: grid;
  grid-template-columns: 1fr 4fr;
  max-width: 100vw;
  margin: 0 auto;
  grid-template-areas: "app-nav app-body"
}
#app-nav{
  grid-area: 'app-nav';
  /* border-right: 2px solid rgb(124, 188, 207); */
  width: 100%;
  height: 100vh;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app-body{
  grid-area: "app-body";
  /* padding: 1em; */
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(254, 255, 255)) , to(rgb(137, 212, 255)));
  background: linear-gradient(to bottom right, rgb(254, 255, 255) , rgb(137, 212, 255));
}

